import {request} from '../network/request'


export function getmusic(){
    return request({
        url: '/music/get'
    })

}
export function jsonmusic(dataload){
    return request({
        url: '/music/load',
        method: 'post',
        data:JSON.stringify(dataload),
        responseType: 'json'
    })

}

export function formmusic(dataload){
    
    console.log(dataload.get('file'))
    return request({
        url: '/music/load',
        method: 'post',
        headers:{
            "Content-Type": "multipart/form-data;",
            'Access-Control-Allow-Origin':'*',
            
        },
        data:dataload
    })

}
export function formmusicfile(dataload){
    return request({
        url: '/music/loadmusic',
        method: 'post',
        headers:{
            "Content-Type": "multipart/form-data;"
        },
        data:dataload
    })

}
export function delmusic(dataload){
    return request({
        url: '/music/delmusic',
        method: 'post',
        headers:{
            "Content-Type": "application/json"
        },
        data:dataload
    })

}




