import Vue from 'vue'
import Vuex from 'vuex'

import { getmusic } from '@/network/mepage.js'

Vue.use(Vuex)
const store = new Vuex.Store({
  state: {
    count: 1000,
    currentuser: {},
    currenmusic:{},
    token: '',
    editor: {
      godEdit: true,
      isEditing: true
    },
    musiclist: [],
  },
  mutations: {
    addtoken(state) {
      state.token = localStorage.getItem('token')
    },
    subtraction(state) {

    },
    commituser(state, user) {
      state.currentuser = user
    },
    updatemusic(state) {
      getmusic().then(res => {
        state.musiclist = res.data
        //console.log(res.data)
      })
    },
    changemusic(state,music){
      state.currenmusic=music
    }


  },
  actions: {

  },
  getters: {

  },
  modules: {}
})
export default store


